html {
  font-size: $font-size;

  @media #{$xxl-width} {
    font-size: ($font-size * 1.175);
  }
}

body {
  margin: 0;
  font-weight: normal;
  font-size: 100%;
  line-height: 1.5;
  font-family: $primary-font;
  color: $base-color;
  overflow-x: hidden;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  background-color: $gray-color-246;
}

.container {
  @include make-container();
}

.container-slim {
  @include make-slim-container();
}

// .container-fat {
//   @include make-fat-container();
// }

.container-fluid {
  @include make-container-fluid();
}

a {
  color: $link-color;
  text-decoration: none;
  outline: none;

  &:not([class]) {
    border-bottom: 1px solid;
  }

  &:hover {
    color: mix(#000, $link-color, 10%);
  }
}

